import { Controller } from '@ali/4ever-cangjie';

type IColsWidthData = Record<string, number>;

type ValueData = Record<string, any>;

export function mergeData(controller: Controller, data: ValueData) {
  const { value } = controller;
  return controller.setData({
    ...value.data,
    ...data,
  });
}

export default function setTableMinColsWidthData(
  controller: Controller,
  data: IColsWidthData,
) {
  const { tableMinColsWidth = {} } = controller.value.data;
  return controller.command(mergeData, {
    tableMinColsWidth: {
      ...tableMinColsWidth,
      ...data,
    },
  });
}
